/* Blog Widget Tabs */
.widget-tabs {
  padding: 0;
  list-style: none;
  margin-top: 25px;
  display: block;
}

.widget-content {
  box-shadow: 0 3px 9px rgba(0, 0, 0, 0.07);
  border-radius: 4px;
  background-color: #fff;
  position: relative;
  width: 100%;
  overflow: hidden;
}

.widget-content:before {
  content: "";
  width: 100%;
  height: 100%;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10;
  background: #fff;
  opacity: 1;
  transition: 0.35s ease-in-out;
}

.widget-content img {
  display: block;
  object-fit: cover;
  width: 100%;
  height: 100%;
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 9;
  opacity: 1;
  transition: 0.35s;
  border-radius: 5.5px;
}

.widget-text {
  display: block;
  padding: 0;
  position: relative;
  z-index: 20;
  padding: 30px;
}

.widget-text h5 {
  font-size: 16px;
  font-weight: 600;
  line-height: 24px;
  margin: 0 0 3px 0;
  color: #333;
  transition: 0.35s;
}

.widget-text span {
  font-size: 14px;
  color: #888;
  display: inline-block;
  background-color: #f0f0f0;
  border-radius: 4px;
  line-height: 20px;
  padding: 4px 10px;
  margin-top: 3px;
  transition: 0.35s;
}

.widget-tabs .widget-content {
  display: table;
}

.widget-tabs .widget-content .widget-text {
  display: table-cell;
  vertical-align: middle;
}

.widget-tabs li {
  margin-top: 20px;
}

.widget-content:hover:before, .widget-content.active:before {
  opacity: 0.6;
  background: #333;
}

.widget-content:hover img, .widget-content.active img {
  opacity: 1;
}

.widget-content:hover h5, .widget-content.active h5 {
  color: #fff;
}

.widget-content:hover span, .widget-content.active span {
  background: rgba(255, 255, 255, 0.25);
  color: #fff;
}

/* Blog Compact Item
------------------------------------- */
.blog-compact-item-container {
