------------------------------------- */
/* Freelancer Socials*/
.freelancer-socials ul {
  margin: 0;
  padding: 0;
  list-style: none;
  cursor: default;
  margin: 0 0 -5px -8px;
}

.freelancer-socials ul li {
  display: inline-block;
  font-size: 24px;
}

.freelancer-socials ul li a {
  color: #b9b9b9;
  padding: 0 8px;
}

.freelancer-socials ul li a:hover {
  color: #333;
}

/* Attachments */
.attachments-container {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  width: calc(100% + 20px);
  margin-bottom: -30px;
}

.attachment-box {
  display: flex;
  margin: 0 20px 20px 0;
  background-color: #f4f4f4;
  border-radius: 4px 0px 4px 4px;
  font-weight: 600;
  padding: 15px 20px;
  padding-bottom: 45px;
  padding-right: 25px;
  line-height: 24px;
  flex-direction: column;
  color: #666;
  position: relative;
  transition: 0.3s;
  flex: 0 1 calc(50% - 21px);
  cursor: default;
  position: relative;
}

.single-page-section .attachment-box {
  flex: 0 1 calc(33% - 20px);
}

.single-page-section .attachments-container {
  margin-bottom: -20px;
}

a.attachment-box {
  cursor: pointer;
}

.attachment-box:before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  border-width: 0 20px 20px 0;
  border-style: solid;
  border-color: rgba(0, 0, 0, 0.15) #fff;
  transition: 0.3s;
  border-radius: 0 0 0 4px;
}

a.attachment-box:hover {
  background-color: #2a41e8;
  color: #fff;
}

a.attachment-box:hover:before {
  border-color: rgba(0, 0, 0, 0.25) #fff;
}

.attachment-box span {
  font-size: 14px;
  line-height: 20px;
  display: inline-block;
  flex: auto;
}

.attachment-box i {
  display: block;
  font-style: normal;
  font-size: 14px;
  color: #999;
  font-weight: 500;
  margin-top: 10px;
  position: absolute;
  bottom: 10px;
  transition: 0.3s;
}

a.attachment-box:hover i {
  color: rgba(255, 255, 255, 0.7);
}

/* Remove Attachment Button */
.attachment-box .remove-attachment {
  position: absolute;
  bottom: 10px;
  right: 10px;
  color: #fff;
  background-color: #dc3139;
  box-shadow: 0 3px 8px rgba(234, 65, 81, 0.15);
  height: 28px;
  width: 28px;
  line-height: 28px;
  border-radius: 3px;
  font-weight: 500;
  font-size: 14px;
  transition: 0.3s;
  opacity: 0;
  transform: translateY(3px);
}

.attachment-box .remove-attachment:before {
  content: "\e9e4";
  font-family: "Feather-Icons";
}

.attachment-box:hover .remove-attachment {
  opacity: 1;
  transform: translateY(0);
}

/* Profile Overview */
.profile-overview {
  display: flex;
  line-height: 22px;
  align-items: center;
  margin-bottom: 25px;
  width: calc(100% + 25px);
}

.profile-overview .overview-item {
  flex: 1;
  border-right: 1px solid #e0e0e0;
  margin-right: 20px;
  padding-right: 20px;
  flex-grow: 1;
}

.profile-overview .overview-item:last-child {
  border-right: 0;
  padding-right: 0;
}

.profile-overview .overview-item strong {
  display: block;
  font-weight: 600;
  color: #333;
  font-size: 18px;
  margin-bottom: 5px;
}

.profile-overview .overview-item span {
  color: #808080;
}

/* Freelancer Indicators */
.freelancer-indicators {
  display: flex;
  flex-wrap: wrap;
  width: calc(100% + 30px);
  line-height: 24px;
  margin-bottom: -20px;
}

.freelancer-indicators .indicator {
  width: calc(50% - 31px);
  margin-right: 30px;
  margin-bottom: 20px;
  display: flex;
  flex-direction: column;
}

.freelancer-indicators .indicator strong {
  color: #333;
  font-weight: 600;
}

.freelancer-indicators .indicator-bar {
  width: 100%;
  height: 4px;
  background-color: #e0e0e0;
  border-radius: 10px;
  position: relative;
  margin: 10px 0;
  transition: 1s;
}

.freelancer-indicators .indicator-bar span {
  width: 100%;
  height: 4px;
  background-color: #e0e0e0;
  border-radius: 10px;
  position: relative;
  margin: 10px 0;
  transition: 1s;
  width: 0;
  background-color: #2a41e8;
  position: absolute;
  top: 0;
  margin: 0;
}

