/* Payment Toggle
------------------------------------- */
.payment-logo {
  height: 24px;
  position: absolute;
  right: 19px;
  top: 18px;
  image-rendering: -webkit-optimize-contrast;
}

.payment-logo.paypal {
  height: 16px;
  top: 22px;
}

.payment-tab-trigger {
  padding: 0px 20px;
}

.payment-tab-trigger > input {
  position: absolute;
  visibility: hidden;
}

.payment-tab-trigger > label {
  cursor: pointer;
  display: block;
  position: relative;
  top: 0;
  padding: 14px 0;
  top: 3px;
  margin-bottom: 0;
  font-weight: 500;
}

.payment-tab-trigger > label::before {
  transition: 0.2s;
  box-sizing: border-box;
  background-color: #fff;
  border: 2px solid #d0d0d0;
  border-radius: 100%;
  content: "";
  display: inline-block;
  margin-right: 10px;
  position: relative;
  top: 3px;
  width: 18px;
  height: 18px;
}

.payment-tab-trigger > label::after {
  background: #fff;
  border-radius: 100%;
  content: "";
  display: inline-block;
  position: absolute;
  left: 5px;
  top: 22px;
  width: 8px;
  height: 8px;
  transition: 0.2s;
  transform: scale(0);
}

.payment-tab-trigger > input:checked ~ label::before {
  border-color: #2a41e8;
}

.payment-tab-trigger > input:checked ~ label::after {
  background-color: #2a41e8;
  transform: scale(1);
}

.payment {
  border: 2px solid #f2f2f2;
  border-radius: 4px;
  overflow: hidden;
}

.payment-tab {
  background: #f2f2f2;
