}

/* Search Button */
.sidebar-search-button-container {
  position: absolute;
  width: calc(100% - 15px);
  bottom: 0;
  padding: 40px;
  padding-top: 10px;
  z-index: 100;
  background-color: #fafafa;
}

.sidebar-search-button-container button {
  width: calc(100% + 15px);
  line-height: 46px;
  height: 46px;
  transition: 0.3s;
  box-shadow: 0 4px 12px rgba(42, 65, 232, 0.1);
}

.sidebar-search-button-container button:hover {
  box-shadow: 0 4px 12px rgba(42, 65, 232, 0.25);
  transform: translateY(-2px);
}

/* Small Footer */
.small-footer {
  border-top: 1px solid #e0e0e0;
  padding-top: 0px;
  width: calc(100% + 80px);
  left: -40px;
  position: relative;
  padding: 25px 40px;
  display: flex;
}

.small-footer strong {
  color: #333;
}

.small-footer-copyrights {
  flex: 2;
}

.small-footer .footer-social-links {
  transform: none;
  flex: 1;
  text-align: right;
