.numbered ol {
  counter-reset: li;
  list-style: none;
  padding: 0;
  margin-left: 18px;
  display: inline-block;
  font-size: 16px;
}

.numbered ol li {
  display: inline-block;
  padding: 6px 0;
  width: 100%;
}

.numbered ol > li::before {
  content: counter(li);
  counter-increment: li;
  font-size: 16px;
  line-height: 35px;
  width: 36px;
  height: 36px;
  display: inline-block;
  border: 1px solid #333;
  border-radius: 50%;
  text-align: center;
  margin: 0;
  margin-left: 0;
  font-weight: 500;
  color: #333;
  position: relative;
  float: left;
  left: -18px;
  font-size: 15px;
  transform: translateY(-10%);
}

.numbered.color ol > li::before {
  border: 1px solid #2a41e8;
  color: #2a41e8;
}

.numbered.color.filled ol > li::before {
  border: 1px solid #2a41e8;
  color: #fff;
  background-color: #2a41e8;
}

/* ---------------------------------- */
/* Marks
------------------------------------- */
mark {
  background-color: #fcf8e3;
  color: #8a803e;
  padding: 1px 5px;
}

mark.color {
  background-color: #2a41e8;
  border-radius: 4px;
  color: #fff;
}

/* ---------------------------------- */
/* Notification Boxes
------------------------------------- */
#result .success {
  line-height: 24px;
  margin-bottom: 15px;
  position: relative;
  padding: 20px 26px;
  padding-right: 50px;
  border-radius: 3px;
}

.notification {
  line-height: 24px;
  margin-bottom: 15px;
  position: relative;
  padding: 20px 26px;
  padding-right: 50px;
  border-radius: 3px;
}

.notification p {
  margin: 0;
  font-size: 15px;
}

#result .success, .notification.success {
  background-color: #EBF6E0;
}

#result .success {
  color: #5f9025;
}

.notification.success {
  color: #5f9025;
}

.notification.success a, .notification.success strong {
  color: #5f9025;
}

.notification.error {
  background-color: #ffe9e9;
  color: #de5959;
}

.notification.error a, .notification.error strong {
  color: #de5959;
}

.notification.warning {
  background-color: #FBFADD;
  color: #8f872e;
}

.notification.warning a, .notification.warning strong {
  color: #8f872e;
}

.notification.notice {
  color: #3184ae;
  background-color: #E9F7FE;
}

.notification.notice h4 {
  font-size: 19px;
  margin: 3px 0 15px 0;
  color: #3184ae;
}

.notification.notice a, .notification.notice strong {
  color: #3184ae;
}

.notification.notice.large {
  padding: 32px 36px;
}

.notification strong {
  font-weight: 700;
}

.notification a {
  text-decoration: underline;
}

body .notification strong {
  border: none;
}

.notification.success .close, .notification.error .close, .notification.warning .close {
  padding: 0px 9px;
  position: absolute;
  right: 0;
  top: 22px;
  display: block;
  height: 8px;
  width: 8px;
  cursor: pointer;
}

.notification.notice .close {
  padding: 0px 9px;
  position: absolute;
  right: 0;
  top: 22px;
  display: block;
  height: 8px;
  width: 8px;
  cursor: pointer;
}

.notification.notice p span i {
  font-weight: 500;
}

.notification a.button {
  float: right;
  color: #fff;
  margin-top: 3px;
}

.notification.notice a.button {
  background-color: #388fc5;
}

.notification.warning a.button {
  background-color: #dfbe51;
}

.notification.error a.button {
  background-color: #d34c4c;
}

.notification.success a.button {
  background-color: #79ba38;
}

.notification.closeable a.close:before {
  content: "\ea02";
  font-family: "Feather-Icons";
  position: absolute;
  right: 25px;
  top: 0;
  cursor: pointer;
  font-weight: 600;
}

/* ---------------------------------- */
/* Tables
------------------------------------- */
table.basic-table {
  width: 100%;
  border-collapse: separate;
  border-spacing: 0;
  border: none;
  margin-bottom: 15px;
  margin-bottom: 0;
}

table.basic-table th {
  background-color: #2a41e8;
  text-align: left;
  color: #fff;
  vertical-align: top;
  font-weight: 500;
  padding: 15px 28px;
}

table.basic-table th:first-child {
  border-radius: 4px 0 0 4px;
}

table.basic-table th:last-child {
  border-radius: 0 4px 4px 0;
}

table.basic-table td {
  padding: 15px 28px;
}

table.basic-table tr:nth-child(odd) {
  background-color: #f4f4f4;
}

@media screen and (max-width: 600px) {
  table {
    border: 0;
  }
  table th {
    display: none;
  }
  table tr {
    display: block;
    margin-bottom: 0;
  }
  table td {
    border-bottom: 1px solid #ddd;
    display: block;
    font-size: 14px;
    text-align: right;
  }
  table td:before {
    content: attr(data-label);
    float: left;
    font-weight: 600;
  }
  table td:last-child {
    border-bottom: 0;
  }
}

/* ---------------------------------- */
/* Accordion
------------------------------------- */
.accordion {
  font-size: 16px;
  width: 100%;
  margin: 0 auto;
  border-radius: 4px;
  overflow: hidden;
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.08);
}

.accordion .accordion {
  border-radius: 0px;
  box-shadow: none;
}

.accordion-header,
.accordion-body {
  background: #fff;
}

.accordion-header {
  padding: 15px 25px;
  background: #2a41e8;
  color: #fff;
  cursor: pointer;
  font-size: 16px;
  transition: all 0.3s;
  position: relative;
  box-shadow: 0 -1px 0 rgba(255, 255, 255, 0.15);
}

.accordion .accordion .accordion-header {
  box-shadow: 0 -1px 0 rgba(0, 0, 0, 0.07);
}

.accordion-header:before {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #fff;
  content: "";
  opacity: 0;
  transition: 0.3s;
}

.accordion-body__contents p:last-child {
  margin-bottom: 0;
  padding-bottom: 0;
}

/*.accordion__item.active .accordion-header:before,
.accordion-header:hover:before { opacity: 0.04; }*/
.accordion-body {
  background: #fff;
  color: #666;
  display: none;
}

.accordion-body__contents {
  padding: 25px;
  font-size: 16px;
  line-height: 28px;
}

.accordion__item.active:last-child .accordion-header {
  border-radius: none;
}

.accordion:first-child > .accordion__item > .accordion-header {
  border-bottom: 1px solid transparent;
}

.accordion__item > .accordion-header:after {
  content: "\e957";
  font-family: Material-Icons;
  font-size: 19px;
  float: right;
  position: relative;
  top: -1px;
  transition: 0.3s all;
  transform: rotate(0deg);
}

.accordion__item.active > .accordion-header:after {
  transform: rotate(-180deg);
}

.accordion__item.active .accordion .accordion-header:before {
  opacity: 0;
}

.accordion__item .accordion__item .accordion-header {
  background: #f7f7f7;
  color: #666;
}

/* ---------------------------------- */
/* Tabs
------------------------------------- */
.tabs {
  z-index: 15px;
  position: relative;
  background: #FFFFFF;
  width: 100%;
  border-radius: 4px;
  box-shadow: 0 4px 18px rgba(0, 0, 0, 0.1);
  box-sizing: border-box;
  margin: 0;
  overflow: hidden;
}

.tabs ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

.tabs-header {
  position: relative;
  background: #2a41e8;
  overflow: hidden;
}

.tabs-header .tab-hover {
  position: absolute;
  bottom: 0;
  left: 0;
  background: #fff;
  width: auto;
  height: 100%;
  transition: 0.3s ease;
  opacity: 0.08;
  z-index: 90;
}

.tabs-header ul {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: calc(100% - 100px);
  position: relative;
  z-index: 100;
}

.tabs-header li {
  transition: 0.3s ease;
}

.tabs-header a {
  z-index: 1;
  display: block;
  box-sizing: border-box;
  padding: 15px 25px;
  color: #fff;
  font-weight: 500;
  text-decoration: none;
}

.tabs-nav {
  position: absolute;
  top: 0;
  right: 0;
  display: flex;
  align-items: center;
  height: 100%;
  padding: 0 15px;
  color: #fff;
  user-select: none;
  font-size: 19px;
}

.tabs-nav i {
  cursor: pointer;
  display: inline-block;
  padding: 0;
  height: 30px;
  width: 30px;
  line-height: 30px;
  background-color: rgba(255, 255, 255, 0.1);
  border-radius: 4px;
  text-align: center;
  position: relative;
  z-index: 101;
  margin-left: 5px;
  transition: 0.3s;
}

.tabs-nav i:hover {
  background-color: rgba(255, 255, 255, 0.15);
}

.tabs-content {
  position: relative;
  transition: 0.3s ease;
  overflow: hidden;
}

.tabs-content .tab {
  padding: 25px;
}

.tabs-content .tab p:last-child {
  margin-bottom: 0;
  padding-bottom: 0;
}

.tabs-content:after {
  content: '';
  position: absolute;
  bottom: -1px;
  left: 0;
  display: block;
  width: 100%;
  height: 1px;
  box-shadow: 0 0 20px 10px #FFFFFF;
}

.tabs-content .tab {
  display: none;
}

.tabs-content .tab.active {
  display: block;
}

@media (max-width: 992px) {
  .tabs-nav {
    display: none;
  }
  .tabs-header ul {
    flex-direction: column;
    width: 100%;
  }
  .tab-hover {
    display: none;
  }
  .tabs-header a {
    box-shadow: 0 -1px 0 rgba(255, 255, 255, 0.15);
  }
  .tabs-header ul li.active a {
    background-color: rgba(255, 255, 255, 0.07);
  }
}

/* ---------------------------------- */
/* Copy to clipboard
------------------------------------- */
.copy-url {
  display: flex;
}

.copy-url input {
  border-radius: 4px 0 0 4px;
  border-right: 0;
}

.copy-url .copy-url-button {
  min-width: 48px;
  background-color: #2a41e8;
  color: #fff;
  border-radius: 0 4px 4px 0;
}

/* ---------------------------------- */
/* Share Buttons
------------------------------------- */
.share-buttons {
  display: block;
}

.share-buttons-trigger {
  display: inline-block;
  height: 44px;
  width: 44px;
  line-height: 44px;
  text-align: center;
  color: #a0a0a0;
  font-size: 18px;
  background-color: #f0f0f0;
  border-radius: 4px;
  transition: 0.4s;
  cursor: default;
}

.share-buttons-trigger i {
  position: relative;
  top: 1px;
}

.share-buttons-content {
  display: inline-block;
  position: relative;
  width: calc(100% - 60px);
}

.share-buttons-content span {
  margin-left: 10px;
  color: #888;
  cursor: default;
  padding: 10px 0;
  display: inline-block;
  transition: 0.4s;
}

.share-buttons-content span strong {
  color: #2a41e8;
  font-weight: 600;
}

.share-buttons:hover .share-buttons-content span {
  opacity: 0;
  transition: 0.2s;
}

/* Icons */
.share-buttons-icons {
  position: absolute;
  left: 5px;
  top: 0;
  padding: 0;
  margin: 0;
  opacity: 0;
  transform: translateX(-10px);
  transition: all 0.4s cubic-bezier(0.5, -0.41, 0.19, 2);
}

.share-buttons:hover .share-buttons-icons {
  opacity: 1;
  transform: translate(0);
}

.share-buttons-icons li {
  display: inline-block;
  float: left;
}

.share-buttons-icons li:first-child a {
  border-radius: 4px 0 0 4px;
}

.share-buttons-icons li:last-child a {
  border-radius: 0 4px 4px 0;
}

.share-buttons-icons li a {
  height: 44px;
  width: 44px;
  display: inline-block;
  line-height: 44px;
  text-align: center;
  background-color: #333;
  color: #fff;
  font-size: 14px;
}

.share-buttons-icons li a i {
  position: relative;
  top: 1px;
}

/* ---------------------------------- */
/* Verified Badge
------------------------------------- */
.job-listing-company .verified-badge, .job-listing .job-listing-footer ul li .verified-badge {
  position: relative;
  display: inline-block;
  transform: scale(0.7);
  top: 2px;
  margin-left: -2px;
}

/* Verified Badge */
.verified-badge {
  position: relative;
  height: 25px;
  width: 25px;
  display: inline-block;
  background-color: #38b653;
  border-radius: 50%;
  text-align: center;
  z-index: 10;
  font-weight: 500;
}

.verified-badge:before {
  content: "\e92b";
  font-family: "Feather-Icons";
  font-size: 16px;
  color: #fff;
  position: relative;
  top: 0px;
  line-height: 27px;
}

.job-listing .job-listing-company-logo .verified-badge {
  position: absolute;
  bottom: 0;
  right: 0;
  transform: translate(7px, 7px);
}

/* Long Verified Badge */
.verified-badge-with-title {
  position: relative;
  height: 26px;
  display: flex;
  top: -1px;
  color: #fff;
  font-weight: 500;
  font-size: 14px;
  background-color: #30ab4a;
  text-align: center;
  z-index: 10;
  font-weight: 500;
  border-radius: 4px;
  padding: 0 8px 0 0;
  margin: 0;
  overflow: hidden;
  padding-left: 34px;
  line-height: 27px;
}

.verified-badge-with-title:before {
  content: "\e92b";
  font-family: "Feather-Icons";
  font-size: 16px;
  color: #fff;
  position: absolute;
  top: 0;
  left: 0;
  line-height: 26px;
  height: 26px;
  width: 26px;
  display: inline-block;
  background-color: #38b653;
}

/* ---------------------------------- */
/* Star Rating
------------------------------------- */
/* Star Rating */
.star-rating {
  display: inline-block;
}

.star-rating .star {
  display: inline-block;
  margin: 0;
  padding: 0;
  float: left;
  margin-right: 0;
  position: relative;
}

.star-rating .star.half:after, .star-rating .star:before {
  font-family: "Material-Icons";
  content: "\e988";
  display: block;
  color: #febe42;
  font-size: 22px;
}

.star-rating .star.empty:before {
  color: #ddd;
}

.rating:after {
  content: ".";
  display: block;
  clear: both;
  visibility: hidden;
  line-height: 0;
  height: 0;
}

.star-rating .star.half:before {
  color: #ddd;
}

.star-rating .star.half:after {
  color: #febe42;
  position: absolute;
  top: 0;
  width: 50%;
  display: block;
  height: 100%;
  overflow: hidden;
}

.star-rating:before {
  content: attr(data-rating);
  float: left;
  border-radius: 4px;
  background-color: #febe42;
  color: #fff;
  font-size: 14px;
  line-height: 15px;
  font-weight: 700;
  padding: 5px 7px;
  position: relative;
  top: 1px;
  margin-right: 10px;
}

.star-rating.no-stars .star {
  display: none;
}

.star-rating.no-stars:before {
  margin-right: 0;
}

/* ---------------------------------- */
/* Leave Rating Stars
------------------------------------- */
.leave-rating {
  height: 24px;
  float: left;
  position: relative;
}

.leave-rating:hover input[type="radio"]:checked ~ label {
  color: #dadada;
}

.leave-rating input[type="radio"] {
  display: inline-block;
  width: 1px;
  position: absolute;
  opacity: 0;
  pointer-events: none;
  height: 100%;
  margin-left: 50%;
  left: 0;
  top: 0;
  transform: translateX(-50%);
}

.leave-rating input[type="radio"]:checked ~ label {
  color: #ffc600;
}

.leave-rating label {
  font-size: 24px;
  float: right;
  letter-spacing: 0px;
  color: #dadada;
  cursor: pointer;
  transition: 0.3s;
}

.leave-rating label:hover {
  color: #ffc600 !important;
}

.leave-rating label:hover ~ label {
  color: #ffc600 !important;
}

.leave-rating-title {
  display: block;
  margin: 0 0 4px 0;
}

.welcome-text .leave-rating-container {
  text-align: center;
  margin: 0 auto;
  margin-top: 15px;
  left: 50%;
  width: 100%;
  display: block;
}

.welcome-text .leave-rating {
  position: relative;
  left: 50%;
  transform: translateX(-50%) scale(1.2);
}

/* ---------------------------------- */
/* Ripple Effect
------------------------------------- */
.ripple-effect-dark {
  overflow: hidden;
  position: relative;
  z-index: 1;
}

.ripple-effect {
  overflow: hidden;
  position: relative;
  z-index: 1;
}

.ripple-effect span.ripple-overlay {
  animation: ripple 0.9s;
  border-radius: 100%;
  background: #fff;
  height: 12px;
  position: absolute;
  width: 12px;
  line-height: 12px;
  opacity: 0.1;
  pointer-events: none;
}

.ripple-effect-dark span.ripple-overlay {
  animation: ripple 0.9s;
  border-radius: 100%;
  background: #fff;
  height: 12px;
  position: absolute;
  width: 12px;
  line-height: 12px;
  opacity: 0.1;
  pointer-events: none;
  background: #000;
  opacity: 0.07;
}

@keyframes ripple {
  0% {
    -webkit-transform: scale(4);
    transform: scale(4);
  }
  100% {
    opacity: 0;
    -webkit-transform: scale(40);
    transform: scale(40);
  }
}

/* ---------------------------------- */
/* On/Off Switch
------------------------------------- */
.switches-list .switch-container {
  width: 100%;
}

.switches-list .switch-container:last-child label {
  margin-bottom: 0;
}

.switch-container {
  display: inline-block;
  cursor: pointer;
}

label.switch {
  position: relative;
}

.switches-list .switch-container label {
  cursor: pointer;
  position: relative;
  padding-left: 45px;
  line-height: 26px;
}

.switch-button {
  position: relative;
  display: inline-block;
  width: 34px;
  height: 20px;
  cursor: pointer;
  background-color: #ccc;
  border-radius: 50px;
  transition: 0.4s;
  margin: 0 7px 0 0;
  top: 4px;
  position: absolute;
  top: 2px;
  left: 0;
}

.notify-box .switch-button {
  top: 0;
}

.notify-box .switch-container {
  top: 1px;
  position: relative;
}

.switch span.switch-text {
  display: block;
  padding-left: 48px;
  font-size: 16px;
  line-height: 22px;
  color: #666;
}

.switch input {
  display: none;
}

.switch.interactive-effect input:checked + .switch-button:before {
  animation: switch-shadow-color 0.4s;
}

.switch.interactive-effect .switch-button:before {
  animation: switch-shadow 0.4s;
}

@keyframes switch-shadow {
  0% {
    box-shadow: 0 0 0 0 rgba(0, 0, 0, 0.3);
  }
  100% {
    box-shadow: 0 0 0px 10px rgba(0, 0, 0, 0);
  }
}

@keyframes switch-shadow-color {
  0% {
    box-shadow: 0 0 0 0 rgba(42, 65, 232, 0.6);
  }
  100% {
    box-shadow: 0 0 0px 10px rgba(0, 0, 0, 0);
  }
}

.switch-button:before {
  position: absolute;
  content: "";
  height: 16px;
  width: 16px;
  bottom: 2px;
  left: 2px;
  background-color: white;
  transition: 0.4s, box-shadow 0.3s;
  border-radius: 50px;
}

input:checked + .switch-button {
  background-color: #2a41e8;
}

input:checked + .switch-button:before {
  transform: translateX(14px);
}

/* ---------------------------------- */
/* Radio Button
------------------------------------- */
.radio {
  display: inline-block;
  vertical-align: top;
}

.radio label {
  margin: 3px 0;
  cursor: pointer;
  position: relative;
  padding-left: 29px;
  line-height: 25px;
}

.radio input[type="radio"] {
  position: absolute;
  opacity: 0;
}

.radio input[type="radio"] + label .radio-label {
  content: '';
  background: #fff;
  border-radius: 100%;
  border: 2px solid #b4b4b4;
  display: inline-block;
  width: 18px;
  height: 18px;
  position: relative;
  margin-right: 5px;
  vertical-align: top;
  cursor: pointer;
  text-align: center;
  transition: all 250ms ease;
  background-color: #fff;
  box-shadow: inset 0 0 0 8px #fff;
  z-index: 100;
  position: absolute;
  top: 2px;
  left: 0;
}

.radio input[type="radio"] + label .radio-label:after {
  background-color: #2a41e8;
  content: "";
  top: 3px;
  left: 0;
  right: 0;
  margin: 0 auto;
  position: absolute;
  opacity: 1;
  transition: 0.3s;
  border-radius: 50%;
  transform: scale(0);
  position: absolute;
  top: 0;
  left: 0;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  z-index: 99;
}

.radio input[type="radio"]:checked + label .radio-label {
  background-color: #2a41e8;
  border-color: #2a41e8;
  box-shadow: inset 0 0 0 3px #fff;
}

.radio-label:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  border-radius: 50%;
  background-color: transparent;
  display: block;
}

.interactive-effect label .radio-label:before {
  animation: radius-shadow 0.4s;
}

@keyframes radius-shadow {
  0% {
    box-shadow: 0 0 0 0 rgba(42, 65, 232, 0.4);
  }
  100% {
    box-shadow: 0 0 0 10px rgba(0, 0, 0, 0);
  }
}

.small-label {
  border-radius: 4px;
  display: inline-block;
  background: #e0f5d7;
  color: #449626;
  padding: 0 8px;
  line-height: 27px;
  height: 26px;
  margin-left: 5px;
  position: relative;
