/* ---------------------------------- */
/* Testimonials
------------------------------------- */
.testimonial-box {
  background: transparent;
  border-radius: 6px;
  padding: 45px;
  padding-top: 35px;
  color: #888;
  position: relative;
  margin: 70px 0 0;
  box-shadow: none;
  transition: 0.4s;
  cursor: grab;
  text-align: center;
  transform: scale(0.8);
}

.testimonial-box:before {
  content: "\e948";
  font-family: "Material-Icons";
  position: absolute;
  top: -35px;
  left: 0;
