  opacity: 0;
  transition: 0.4s;
  pointer-events: all;
}

.testimonial-carousel .slick-slide.slick-active {
  opacity: 1;
  filter: none;
  pointer-events: all;
}

.testimonial-carousel .slick-slide.slick-active .testimonial-box {
  transform: scale(1);
  background: #fff;
  color: #666;
  box-shadow: 0 0 30px rgba(0, 0, 0, 0.08);
