/* ---------------------------------- */
/* Slick Slider
------------------------------------- */
/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-touch-callout: none;
  -khtml-user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  display: block;
  overflow: hidden;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track, .slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  top: 0;
  left: 0;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.slick-track:before {
  display: table;
  content: '';
}

.slick-track:after {
  display: table;
  content: '';
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  display: none;
  float: left;
  height: 100%;
  min-height: 1px;
}

[dir='rtl'] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.slick-arrow.slick-hidden {
  display: none;
}

/* Slider */
/*.slick-loading .slick-list {
    background: #fff url('./ajax-loader.gif') center center no-repeat;
}
*/
/* Arrows */
.slick-prev, .slick-next {
  font-size: 0;
  line-height: 0;
  position: absolute;
  top: 50%;
  display: block;
  padding: 0;
  -webkit-transform: translate(0, -50%);
  -ms-transform: translate(0, -50%);
  transform: translate(0, -50%);
  cursor: pointer;
  border: none;
  outline: none;
  background-color: #333;
  border-radius: 4px;
  width: 48px;
  height: 48px;
  display: block;
  top: calc(50% - 15px);
  transform: translateY(-50%);
  transition: 0.3s;
  box-shadow: 0 3px 12px rgba(0, 0, 0, 0.15);
  z-index: 10;
}

.slick-prev:hover, .slick-next:hover {
  color: transparent;
  outline: none;
  background: #2a41e8;
}

.slick-prev:hover:before, .slick-next:hover:before {
  opacity: 1;
}

.slick-prev.slick-disabled, .slick-next.slick-disabled {
  pointer-events: none;
  cursor: default;
  background-color: #e8e8e8;
  box-shadow: none;
}

.slick-prev.slick-disabled:before, .slick-next.slick-disabled:before {
  color: #808080;
}

.slick-prev:before, .slick-next:before {
  font-family: "Feather-Icons";
  font-size: 26px;
  line-height: 1;
  opacity: 1;
  transition: 0.3s;
  position: relative;
  color: #fff;
}

.slick-prev {
  left: -85px;
}

.slick-prev:before {
  content: '\e92f';
  left: -1px;
}

.slick-next {
  right: -85px;
}

.slick-next:before {
  content: '\e930';
  right: -1px;
}

/* Dots */
.slick-dots {
  margin: 0 auto;
  padding: 0;
  text-align: center;
}

.slick-dots li {
  position: relative;
  display: inline-block;
  padding: 0;
  margin: 0 3px;
  cursor: pointer;
  font-size: 0;
  line-height: 0;
  cursor: pointer;
  color: transparent;
  border: 0;
  position: relative;
  outline: none;
  display: inline-block;
  width: 15px;
  height: 15px;
  margin: 0 3px;
  opacity: 1;
  border-radius: 50%;
  line-height: 0;
  box-shadow: inset 0 0 0 2px #c0c0c0;
  transition: all 0.3s;
}

.slick-dots li.slick-active {
  box-shadow: inset 0 0 0 6px #c0c0c0;
  transition: all 0.3s;
}

.slick-dots li:after {
  display: block;
  height: 13px;
  width: 13px;
  content: "";
  position: absolute;
  top: 1px;
  left: 1px;
  background-color: transparent;
  border-radius: 50%;
  transition: all 0.3s;
  opacity: 0;
  background-color: #c0c0c0;
}

.slick-dots li.slick-active:after {
  opacity: 1;
}

/* Reset */
.slick-slide {
  display: none;
