  top: 50%;
  right: 0;
  position: absolute;
  margin: 0;
  opacity: 0;
  transform: scale(0.5);
}

.bootstrap-select.btn-group .dropdown-menu li.selected span.check-mark:before {
  opacity: 1;
  transform: scale(1);
  transition: all 0.35s cubic-bezier(0.3, -0.41, 0.19, 2), opacity 0.3s;
}

