  cursor: pointer;
}

.avatar-wrapper .profile-pic {
  height: 100%;
  width: 100%;
  transition: all .3s ease;
  object-fit: cover;
}

.avatar-wrapper .profile-pic:after {
  font-family: Feather-Icons;
  content: "\e9f1";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  line-height: 120px;
  position: absolute;
  font-size: 60px;
  background: #f0f0f0;
  color: #aaa;
  text-align: center;
}

.avatar-wrapper .upload-button {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
}

.avatar-wrapper .file-upload {
  opacity: 0;
  pointer-events: none;
  position: absolute;
}

/* Small Footer Styles
------------------------------------- */
.dashboard-content-inner .small-footer {
  width: 100%;
  left: 0px;
  padding: 25px 50px;
  position: absolute;
  bottom: 0;
}

.dashboard-footer-spacer {
  padding-top: 125px;
}

/* Dashbaord Media Queries 
------------------------------------- */
@media (min-width: 1365px) and (max-width: 1600px) {
  .fun-fact:last-child {
    display: none;
  }
}

@media (max-width: 1365px) {
  .fun-fact {
    flex: 1 1 calc(50% - 30px);
    max-width: calc(50% - 30px);
  }
}

@media (max-width: 992px) {
  .dashboard-content-inner {
    min-height: auto !important;
  }
  /* Dashboard Mobile Basis */
  .dashboard-sidebar .dashboard-nav-container, .dashboard-content-inner {
    padding: 15px;
  }
  .dashboard-sidebar-inner {
    padding-bottom: 30px;
  }
  .dashboard-sidebar .simplebar-content {
    overflow-x: hidden;
  }
  .dashboard-nav-inner {
    padding: 30px 0;
    margin-bottom: 0;
  }
  .dashboard-sidebar {
    flex: 1;
    width: 100%;
    background-color: transparent;
    box-shadow: none;
    height: auto !important;
  }
  .full-page-sidebar .simplebar-track, .dashboard-sidebar .simplebar-track {
    display: none;
  }
  .full-page-sidebar-inner, .dashboard-sidebar-inner {
    height: auto !important;
  }
  .dashboard-container {
    display: block;
    width: 100%;
    flex-wrap: wrap;
    height: auto !important;
  }
  .dashboard-content-container {
    height: auto !important;
  }
  .dashboard-content-container .simplebar-scrollbar {
    display: none;
  }
  /* Dashboard Mobile Basis - End */
  /* Dashboard Nav */
  .dashboard-nav {
    width: 100%;
    margin: 0;
    border-radius: 0 0 4px 4px;
    position: relative;
    min-height: auto;
    opacity: 1;
    visibility: hidden;
    max-height: 0;
    padding: 0;
    transition: all 0.3s;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    background: #fff;
  }
  .dashboard-nav ul {
    margin-top: 15px;
  }
  .dashboard-nav ul:first-child {
    margin: 0;
  }
  .dashboard-nav.active {
    opacity: 1;
    visibility: visible;
    max-height: 900px;
  }
  .dashboard-nav.active ul {
    opacity: 1;
    visibility: visible;
    max-height: 900px;
  }
  .dashboard-nav ul {
    visibility: hidden;
    opacity: 0;
    max-height: 0;
    transition: all 0.3s;
  }
  .dashboard-nav ul:before {
    padding: 0 29px;
  }
  .dashboard-nav ul li a {
    padding: 10px 25px;
  }
  .dashboard-nav.active ul li ul, .dashboard-nav ul li ul {
    visibility: hidden;
    opacity: 0;
    max-height: 0;
    transition: all 0.3s;
  }
  .dashboard-nav.active ul li.active-submenu ul {
    visibility: visible;
    opacity: 1;
    max-height: 300px;
  }
  /* Trigger */
  a.dashboard-responsive-nav-trigger {
    width: 100%;
    margin: 0;
    margin-top: 15px;
    background-color: #333;
    border-radius: 5px;
    color: #fff;
    padding: 16px 30px;
    font-weight: 600;
    transition: 0.2s;
    display: block;
    line-height: 26px;
  }
  a.dashboard-responsive-nav-trigger span.trigger-title {
    padding-left: 33px;
  }
  a.dashboard-responsive-nav-trigger .hamburger-inner {
    background-color: #fff;
  }
  a.dashboard-responsive-nav-trigger .hamburger-inner::before, a.dashboard-responsive-nav-trigger .hamburger-inner::after {
    background-color: #fff;
  }
  a.dashboard-responsive-nav-trigger .hamburger {
    top: 2px;
    left: 0;
    transform: scale(0.66);
  }
  a.dashboard-responsive-nav-trigger i {
    margin-right: 8px;
    margin-left: -2px;
    font-size: 28px;
    line-height: 0;
    position: relative;
    top: 6px;
  }
  a.dashboard-responsive-nav-trigger.active {
    border-radius: 4px 4px 0 0;
  }
  /* Dashboard Nav - End */
  .buttons-to-right:not(.single-right-button) {
    opacity: 1;
  }
  .fun-facts-container {
    width: calc(100% + 15px);
    margin-bottom: -15px;
  }
  .fun-fact {
    flex: 1 1 calc(50% - 15px);
    max-width: calc(50% - 15px);
    margin: 0 15px 15px 0;
  }
}

@media (max-width: 768px) {
  .fun-fact {
    flex: 1 1 100%;
    max-width: 100%;
  }
  #titlebar #breadcrumbs, .dashboard-headline #breadcrumbs {
    right: 0;
    position: relative;
    top: 0;
    transform: none;
    margin-top: 30px;
    max-width: 100%;
  }
  .buttons-to-right:not(.single-right-button) {
    position: relative;
    transform: none;
    top: 0;
    margin: 10px 0 0 0;
    opacity: 1;
    right: auto;
    display: inline-block;
    box-shadow: none;
    width: 100%;
  }
  .dashboard-box .headline .sort-by {
    float: none;
    display: inline-block;
    position: relative;
    top: 0;
    transform: none;
    right: 0px;
    top: 5px;
    left: -8px;
  }
  .dashboard-box .headline .sort-by .bootstrap-select .dropdown-menu {
    left: 0;
    right: auto;
  }
}

/* ---------------------------------- */
/* Dashboard Messages
------------------------------------- */
.messages-container {
  margin: 0;
  box-shadow: 0 0 12px 0 rgba(0, 0, 0, 0.06);
  border-radius: 4px;
  background-color: #fff;
}

/* Scrollbar Adjustments */
.messages-inbox ul {
  max-height: 875px;
}

.message-content-inner {
  max-height: 750px;
}

.message-content, .messages-container-inner .messages-inbox {
  overflow: hidden;
}

.message-content-inner, .messages-container-inner .messages-inbox ul {
  overflow-y: scroll;
  width: calc(100% + 18px);
}

.message-content-inner::-webkit-scrollbar, .messages-container-inner .messages-inbox ul::-webkit-scrollbar {
  width: 17px;
}

/*  Scrollbar Hide - End */
.messages-headline {
  max-height: 82px;
  font-size: 16px;
  margin: 0;
  padding: 22px 30px;
  color: #333;
  background-color: #fff;
  display: block;
  border-bottom: 1px solid #eaeaea;
  border-radius: 4px 4px 0 0;
  position: relative;
}

.messages-headline input {
  background-color: #f6f6f6;
  box-shadow: none;
  height: 38px;
  padding: 14px;
  font-size: 14px;
  margin: 0;
}

.messages-headline input:focus {
  box-shadow: none;
}

.messages-headline .input-with-icon i {
  font-size: 18px;
  right: 14px;
}

.messages-headline h4 {
  font-size: 16px;
  color: #333;
  padding: 6px 0;
  margin: 0;
  font-weight: 600;
  display: inline-block;
}

.message-action {
  border-radius: 50px;
  font-size: 13px;
  color: #666;
  font-weight: 500;
  display: inline-block;
  transition: 0.3s;
  color: #666;
  padding: 0;
  position: absolute;
  top: 50%;
  right: 30px;
  transform: translateY(-50%);
}

.message-action i {
  font-size: 14px;
  position: relative;
  top: 1px;
  margin-right: 1px;
}

.message-action:hover {
  color: #ee3535;
}

/* List */
.messages-inbox ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.message-avatar img {
  display: inline-block;
  width: 54px;
  height: 54px;
  border-radius: 50%;
}

.messages-inbox ul li {
  transition: 0.2s;
  position: relative;
}

.messages-inbox ul li a {
  position: relative;
  display: block;
  padding: 30px;
}

.messages-inbox ul li:nth-child(2n) {
  background-color: #fafafa;
}

.messages-inbox ul li:last-child {
  border-bottom: none;
}

.messages-inbox ul li.active-message {
  background-color: #fafafa;
}

.messages-inbox ul li:before {
  position: absolute;
  top: 0;
  left: 0;
  width: 3px;
  height: 100%;
  background-color: #2a41e8;
  display: block;
  content: "";
  opacity: 0;
  transition: 0.3s;
}

.messages-inbox ul li:hover:before, .messages-inbox ul li.active-message:before {
  opacity: 1;
}

.messages-inbox .message-avatar {
  position: absolute;
  left: 30px;
  top: 50%;
  transform: translateY(-50%);
}

.messages-inbox .message-by {
  margin-left: 85px;
  font-size: 15px;
  color: #666;
}

.messages-inbox .message-by p {
  margin: 0;
  padding: 0;
  line-height: 25px;
  font-size: 15px;
}

.message-by-headline h5 {
  display: inline-block;
}

.message-by-headline span {
  display: inline-block;
  float: right;
  color: #888;
  font-size: 14px;
}

.message-by-headline h5 {
  font-weight: 600;
  color: #333;
  margin: 0 0 7px 0;
  padding: 0;
}

.message-by-headline:after {
  content: ".";
  display: block;
  clear: both;
  visibility: hidden;
  line-height: 0;
  height: 0;
}

.message-by h5 i {
  background-color: #2a41e8;
  border-radius: 4px;
  line-height: 20px;
  font-size: 12px;
  color: #fff;
  font-style: normal;
  padding: 3px 8px;
  margin-left: 3px;
}

/* Single Message */
.messages-container-inner {
  display: flex;
}

.messages-container-inner .messages-inbox, .messages-container-inner .message-content {
  flex: 1;
}

.messages-container-inner .message-content-inner {
  padding: 30px;
  padding-bottom: 0;
  flex-grow: 1;
}

.messages-container-inner .message-content {
  display: flex;
  flex-direction: column;
}

.messages-container-inner .messages-inbox {
  max-width: 340px;
  border-right: 1px solid #eaeaea;
}

.messages-container-inner .messages-inbox ul li a {
  padding: 25px;
}

.messages-container-inner .messages-inbox .message-by p {
  height: 26px;
  max-width: 205px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  font-size: 14px;
  color: #888;
}

.messages-container-inner .message-avatar img {
  width: 40px;
  height: 40px;
}

.messages-container-inner .messages-inbox .message-avatar {
  left: 25px;
}

.messages-container-inner .messages-inbox .message-by {
  margin-left: 60px;
}

.messages-container-inner .message-by-headline h5 {
  font-size: 14px;
  margin-bottom: 3px;
  max-width: 160px;
  line-height: 23px;
}

.messages-container-inner .message-by-headline span {
  font-size: 13px;
  position: absolute;
  top: 25px;
  right: 25px;
}

/* Message Bubbles */
.message-bubble {
  display: block;
  position: relative;
  margin-bottom: 25px;
  /* Message Bubble "me" */
}

.message-bubble .message-avatar {
  position: absolute;
  left: 0;
  top: 0;
}

.message-bubble .message-avatar img {
  width: 50px;
  height: 50px;
}

.message-bubble .message-text {
  margin-left: 70px;
  background-color: #f4f4f4;
  border-radius: 4px;
  padding: 12px 18px;
  position: relative;
  display: inline-block;
  float: left;
  line-height: 25px;
}

.message-bubble.me .message-text {
  float: right;
}

.message-bubble .message-text p {
  font-size: 15px;
  padding: 0;
  margin: 0;
  line-height: 25px;
}

.message-bubble.me .message-avatar {
  left: auto;
  right: 0;
}

.message-bubble.me .message-text {
  margin-left: 0;
  margin-right: 70px;
  background-color: #2a41e8;
  color: #fff;
  position: relative;
}

.message-bubble .message-text:before {
  content: "";
  width: 0;
  height: 0;
  border-top: 6px solid transparent;
  border-bottom: 6px solid transparent;
  border-right: 6px solid #f4f4f4;
  left: -6px;
  right: auto;
  top: 19px;
  position: absolute;
}

.message-bubble.me .message-text:before {
  border-top: 6px solid transparent;
  border-bottom: 6px solid transparent;
  border-left: 6px solid #2a41e8;
  border-right: none;
  right: -6px;
  left: auto;
}

/* Message Time Sign */
.message-time-sign {
  text-align: center;
  display: block;
  position: relative;
  margin-bottom: 25px;
}

.message-time-sign:before {
  content: "";
  height: 1px;
  background-color: #eaeaea;
  display: block;
  max-width: 30%;
  margin: 0 auto;
  position: relative;
  top: 14px;
  z-index: 10;
}

.message-time-sign span {
  background-color: #fff;
  font-size: 14px;
  color: #888;
  z-index: 11;
  position: relative;
  padding: 0 10px;
}

/* Reply */
.message-reply {
  border-top: 1px solid #eaeaea;
  padding: 30px;
  display: flex;
  align-items: flex-start;
  margin-top: 15px;
}

.message-reply textarea {
  min-width: auto;
  height: 48px;
  min-height: 48px;
  padding: 10px 0;
  margin: 0 15px 0 0;
  flex: 1;
  font-size: 15px;
  font-weight: 400;
  line-height: 26px;
  box-shadow: none;
  border: none;
  box-sizing: border-box;
  resize: none;
}

.message-reply button {
  border-radius: 4px;
  flex: auto;
  flex-grow: 0;
  height: 44px;
  text-align: center;
  align-self: center;
  padding: 0 25px;
}

.message-reply i {
  padding: 0;
  margin: 0;
}

/* Typing Indicastor */
.typing-indicator {
  width: auto;
  height: 24px;
  padding-top: 8px;
}

.typing-indicator span {
  height: 8px;
  width: 8px;
  float: left;
  margin: 0 1px;
  background-color: #a0a0a0;
  display: block;
  border-radius: 50%;
  opacity: 0.4;
}

.typing-indicator span:nth-of-type(1) {
  animation: 1s blink infinite 0.33333s;
}

.typing-indicator span:nth-of-type(2) {
  animation: 1s blink infinite 0.66666s;
}

.typing-indicator span:nth-of-type(3) {
  animation: 1s blink infinite 0.99999s;
}

@keyframes blink {
  50% {
    opacity: 1;
  }
}

/* Messages Media Queries */
@media (max-width: 992px) {
  .messages-container-inner .messages-inbox {
    max-width: 300px;
  }
}

@media (max-width: 768px) {
  .messages-container-inner {
    display: block;
  }
  .messages-container-inner .messages-inbox {
    max-width: 100%;
    border-bottom: 1px solid #e4e4e4;
    border-right: none;
  }
  .messages-inbox ul {
    max-height: 500px;
  }
  .messages-container-inner .message-by-headline span {
    position: relative;
    top: 0;
    right: 0;
    display: block;
    float: none;
    padding: 0;
    margin: -4px 0 0 0;
  }
  .message-action {
    display: block;
    position: relative;
    right: 0;
    top: 0;
    transform: none;
    margin-top: -5px;
  }
  .messages-headline {
    max-height: 120px;
  }
}

/* -------------------------------------------------------------- */
/* 06. Layout Basics
----------------------------------------------------------------- */
/* ---------------------------------- */
/* Common Styles
------------------------------------- */
body {
  color: #666;
  font-size: 16px;
  line-height: 27px;
  background-color: #fff;
  font-family: "Nunito", sans-serif;
  text-transform: none;
}

body.gray {
  background-color: #fbfbfb;
}

body p {
  font-size: 16px;
  line-height: 27px;
}

/* Main Font */
h1, h2, h3, h4, h5, h6 {
  font-family: "Nunito", sans-serif;
  text-transform: none;
}

input[type="text"], input[type="password"], input[type="email"] {
  font-family: "Nunito", sans-serif;
  text-transform: none;
}

textarea, select {
  font-family: "Nunito", sans-serif;
  text-transform: none;
}

input[type="button"], input[type="submit"] {
  font-family: "Nunito", sans-serif;
  text-transform: none;
}

button, body .pac-container {
  font-family: "Nunito", sans-serif;
  text-transform: none;
}

/* Headlines */
h1, h2, h3, h4, h5, h6 {
  color: #333;
  font-weight: 500;
  margin: 0;
  padding: 0;
}

h1 {
  font-size: 32px;
}

h2 {
  font-size: 26px;
}

h3 {
  font-size: 22px;
}

h4 {
  font-size: 16px;
}

h5 {
  font-size: 14px;
}

h6 {
  font-size: 12px;
}

h3.page-title {
  font-size: 20px;
  line-height: 32px;
}

.section {
  display: block;
}

.section.white {
  background: #fff;
}

.section.gray {
  background: #f9f9f9;
}

.section.border-top {
  border-top: 1px solid #e0e0e0;
}

.section.border-bottom {
  border-bottom: 1px solid #e0e0e0;
}

/* Section Headline */
.section-headline {
  display: block;
  position: relative;
  padding-right: 20%;
}

.section-headline.centered {
  text-align: center;
  padding: 0;
}

.section-headline span {
  font-size: 18px;
  line-height: 26px;
  font-weight: 300;
  color: #888;
  margin-top: 0;
  display: block;
}

.section-headline h1, .section-headline h2, .section-headline h3 {
  font-size: 26px;
  line-height: 36px;
  font-weight: 500;
  color: #333;
}

.section-headline h4 {
  font-size: 26px;
  line-height: 36px;
  font-weight: 500;
  color: #333;
  font-size: 22px;
  line-height: 34px;
}

.section-headline.border-top {
  border-top: 1px solid #e0e0e0;
}

h1.in-a-box, h2.in-a-box, h3.in-a-box, h5.in-a-box {
  display: inline-block;
  font-size: 18px;
  background-color: #333;
  color: #fff;
  border-radius: 4px;
  padding: 8px 20px;
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.15);
}

.section-headline h5 {
  font-size: 16px;
  font-weight: 600;
  line-height: 30px;
}

/* 
/* Single Task / Job / Profile Headings */
.single-page-section {
  margin-bottom: 55px;
  display: block;
}

.single-page-section p:last-of-type {
  padding-bottom: 0;
  margin-bottom: 0;
}

.single-page-section h3 {
  margin-bottom: 20px;
  font-size: 20px;
}

.single-page-section .job-listing h3.job-listing-title {
  margin-bottom: 0;
}

/* Headline Link */
a.headline-link {
  position: absolute;
  right: 0;
  bottom: 0;
}

a.headline-link.bottom-icon:after {
  content: "\e957";
  top: 3px;
}

a.headline-link:after {
  content: "\e995";
  font-family: "Material-Icons";
  font-size: 21px;
  line-height: 0;
  position: relative;
  top: 4px;
  margin-left: 5px;
}

@media (max-width: 768px) {
  a.headline-link {
    position: relative;
    margin-top: 10px;
    display: inline-block;
  }
}

/* Classes for disabling things on mobile devices */
@media (max-width: 992px) {
  .hide-under-992px {
    display: none;
  }
}

@media (max-width: 768px) {
  .hide-under-768px {
    display: none;
  }
}

@media (max-width: 480px) {
  .hide-under-480px {
    display: none;
  }
}

/* Content & Sidebar Offsets */
.content-left-offset {
  padding-left: 30px;
}

.content-right-offset {
  padding-right: 30px;
}

@media (max-width: 992px) {
  .content-left-offset {
    padding-left: 15px;
  }
  .content-right-offset {
    padding-right: 15px;
  }
}

/* ---------------------------------- */
/* Icons Cheatsheet Styles
------------------------------------- */
.icon-set-container {
  display: block;
  width: calc(100% + 30px);
  left: -15px;
  position: relative;
  display: flex;
  flex-wrap: wrap;
  align-content: center;
  justify-content: center;
}

.glyph.fs1 {
  position: relative;
  font-size: 20px;
  margin: 10px 15px;
  background-color: #fff;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.08);
  border-radius: 4px;
  flex: 1 1 calc(25% - 30px);
  align-content: center;
  justify-content: center;
  display: flex;
  flex-direction: column;
  padding: 15px 15px;
  transition: 0.3s;
  color: #666;
}

.glyph.fs1:hover {
  transform: translateY(-5px);
  background-color: #333;
  color: #fff;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
}

.glyph.fs1:hover span:not([class*="mls"]) {
  background-color: #fff;
  color: #333;
}

.glyph.fs1 .fs0, .glyph.fs1 fieldset {
  display: none;
}

.glyph.fs1 span:not([class*="mls"]) {
  vertical-align: middle;
